body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 550px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

h1 {
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  color: rgba(45, 20, 200, 0.25);
  text-rendering: optimizeLegibility;
}

:focus {
  outline: 0;
}

::-webkit-input-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

::-moz-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

:-ms-input-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

::-ms-input-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

::placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

.notepad {
  background: #fff;
  margin-bottom: 50px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
  0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.new-todo {
  width: 330px;
  font-size: 24px;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4em;
  border: 0;
  color: inherit;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.list {
  position: relative;
  border-top: 1px solid #e6e6e6;
}

.list:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
  0 8px 0 -3px #f6f6f6,
  0 9px 1px -3px rgba(0, 0, 0, 0.2),
  0 16px 0 -6px #f6f6f6,
  0 17px 2px -6px rgba(0, 0, 0, 0.2);
}


.list ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list ul li {
  position: relative;
  font-size: 24px;
  border-bottom: 1px solid #ededed;
  word-break: break-all;
  padding: 15px 15px 15px 60px;
  line-height: 1.2;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
}

.list ul li label {
  font-size: 12px;
}

.list ul li:last-child {
  border-bottom: none;
}

.list ul li .remove {
  display: none;
  position: absolute;
  top: 0;
  right: 10px;
  bottom: 0;
  width: 40px;
  height: 40px;
  margin: auto 0 auto 11px;
  font-size: 30px;
  color: #cc9a9a;
  -webkit-transition: color 0.2s ease-out;
  transition: color 0.2s ease-out;
  cursor: pointer;
  padding: 0;
  border: 0;
  background: none;
  vertical-align: baseline;
  font-family: inherit;
  font-weight: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (hover: none) and (pointer: coarse) {
  .list ul li:hover .remove {
    display: block;
  }
}

.list ul li .remove:hover {
  color: #af5b5e;
}

.list ul li .remove:after {
  content: '×';
}

.list ul li:hover .remove {
  display: block;
}

.todo-meta {
  position: absolute;
  top: 0;
  right: 50px;
  bottom: 0;
  width: 60px;
  height: 40px;
  margin: auto 0 auto 11px;
  padding-top: 22px;
}

.metaline {
  font-size: 12px;
  line-height: 0.6;
  padding: 2px;
}

.metalinelabel,
.metalinenumber {
  display: inline-block;
}

.metalinelabel {
  margin-right: 2px;
  text-align: right;
  width: 40px;
}

.todo-text {
  display: inline-block;
  max-width: 330px;
}

.ei-wrapper input {
  position: absolute;
  right: 50px;
  height: 33px;
  border: 1px solid lightgrey;
  width: 50px;
  padding: 0 2px 0 8px;
  /*font-size: 18px;*/
  font-family: inherit;
  font-weight: inherit;
}

.ei-wrapper input.effort {
  top: 0;
  border-top: 0;
  border-right: 0;
}

.ei-wrapper input.impact {
  bottom: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 0;
}

.add-button {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 50px;
}

form {
  position: relative;
  background: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
}

.text-input {
  display: inline-block;
  padding: 16px 16px 16px 60px;
}

@media only screen and (max-width: 600px) {
  .text-input {
    padding: 16px 16px 16px 30px;
  }

  .list ul li {
    padding: 15px 15px 15px 30px;
  }

  .todo-text {
    max-width: 264px;
  }
}
